import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  M_Token: localStorage.getItem("M_Token")
    ? localStorage.getItem("M_Token")
    : null,
  M_UserInfo: localStorage.getItem("M_UserInfo")
    ? JSON.parse(localStorage.getItem("M_UserInfo"))
    : null,
};

const UserSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    M_Token: (state, action) => {
      state.M_Token = action.payload;
      localStorage.setItem("M_Token", action.payload);
    },
    M_UserInfo: (state, action) => {
      state.M_UserInfo = action.payload;
      localStorage.setItem("M_UserInfo", JSON.stringify(action.payload));
    },
  },
});

export const { M_Token, M_UserInfo } = UserSlice.actions;
export default UserSlice.reducer;
