import React, { useEffect, useRef, useState } from "react";
import SingleMail from "./SingleMail";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { Button, Col, Row } from "react-bootstrap";
import MailDetailBox from "./MailDetailBox";
import NoData from "../../Assets/NoData";
import { AllcountSlice } from "../../ReduxToolkit/EmailSlice";
import StarIcon from "../../Assets/StarIcon";
import Loader from "./Loader";
import { toast } from "react-toastify";

const MailListBox = (props) => {
  let dispatch = useDispatch();
  let Token = useSelector((state) => state.UserData.M_Token);
  const [MailList, setMailList] = useState({ Loading: false, data: [] });
  const [MailDetail, setMailDetail] = useState({ Loading: false, data: [] });
  const [NextShow, setNextShow] = useState(false);
  const [PrevShow, setPrevShow] = useState(false);
  const nextPrevRef = useRef(0);

  async function getEmailList(number) {
    setMailList({ Loading: true, data: [] });
    setMailDetail({ Loading: true, data: [] });

    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    const config = {
      headers: headers,
    };
    try {
      const Response = await axios.get(
        `https://graph.microsoft.com/v1.0/me/mailFolders('${props.type}')/messages?%24top=10&%24skip=${number}`,
        // `https://graph.microsoft.com/v1.0/me/messages`,
        config
      );

      if (Response?.data?.value?.length < 10) {
        setNextShow(false);
        setPrevShow(nextPrevRef.current > 0);
      } else {
        setNextShow(true);
        setPrevShow(nextPrevRef.current > 0);
      }

      setMailList({ Loading: false, data: Response?.data?.value });
      setMailDetail({ Loading: false, data: Response?.data?.value[0] });
    } catch (error) {
      console.log(error);
    }
  }

  useEffect(() => {
    getEmailList(1);
  }, [props]);

  function fnNextPrev(type) {
    let newNumber = nextPrevRef.current;
    if (type === "next") {
      newNumber = nextPrevRef.current + 10;
    }
    if (type === "prev" && nextPrevRef.current >= 10) {
      newNumber = nextPrevRef.current - 10;
    }
    nextPrevRef.current = newNumber;
    getEmailList(newNumber);
  }
  function getEmailDetail(data) {
    setMailDetail({ Loading: false, data: data });
  }

  async function InboxCount() {
    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.get(
        "https://graph.microsoft.com/v1.0/me/mailFolders/Inbox/messages?$count=true",
        {
          headers: headers,
        }
      );
      const inboxCount = response?.data["@odata.count"];
      return inboxCount;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
  async function JunkCount() {
    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.get(
        "https://graph.microsoft.com/v1.0/me/mailFolders/JunkEmail/messages?$count=true",
        {
          headers: headers,
        }
      );
      const inboxCount = response?.data["@odata.count"];
      return inboxCount;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
  async function DraftsCount() {
    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.get(
        "https://graph.microsoft.com/v1.0/me/mailFolders/Drafts/messages?$count=true",
        {
          headers: headers,
        }
      );
      const inboxCount = response?.data["@odata.count"];
      return inboxCount;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
  async function SentCount() {
    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.get(
        "https://graph.microsoft.com/v1.0/me/mailFolders/SentItems/messages?$count=true",
        {
          headers: headers,
        }
      );
      const inboxCount = response?.data["@odata.count"];
      return inboxCount;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
  async function DeleteCount() {
    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.get(
        "https://graph.microsoft.com/v1.0/me/mailFolders/DeletedItems/messages?$count=true",
        {
          headers: headers,
        }
      );
      const inboxCount = response?.data["@odata.count"];
      return inboxCount;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }

  async function fetchCounts() {
    try {
      const [inboxCount, junkCount, draftsCount, sentCount, deletecount] =
        await Promise.all([
          InboxCount(),
          JunkCount(),
          DraftsCount(),
          SentCount(),
          DeleteCount(),
        ]);
      let AllCount = {
        inbox: inboxCount,
        junk: junkCount,
        drafts: draftsCount,
        sent: sentCount,
        delete: deletecount,
      };
      dispatch(AllcountSlice(AllCount));
    } catch (error) {
      console.error(error);
    }
  }

  async function Deletemail(id) {
    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.delete(
        `https://graph.microsoft.com/v1.0/me/mailFolders('${props.type}')/messages/${id}`,
        {
          headers: headers,
        }
      );
      if (response.status === 204) {
        toast.success("You have deleted successfully!");
        getEmailList(nextPrevRef?.current);
        fetchCounts();
      }
    } catch (error) {
      console.error(error);
      throw error;
    }
  }

  return (
    <>
      <Row>
        <Col sm={MailDetail?.data !== undefined && 5}>
          <div
            // className="maillist mt-2"
            className={MailList?.data?.length > 0 ? "maillist mt-2" : "mt-2"}
          >
            <div className="maillist-header p-2 d-flex justify-content-between align-items-center">
              <p className="m-0 text-capitalize">
                <span className="me-2">{props?.type}</span> <StarIcon />
              </p>
              <div className=" justify-content-end d-flex">
                {PrevShow && (
                  <Button
                    onClick={() => fnNextPrev("prev")}
                    variant="primary me-2 np-btn"
                  >
                    <svg
                      fill="#fff"
                      width="20px"
                      height="20px"
                      viewBox="-78.5 0 512 512"
                      xmlns="http://www.w3.org/2000/svg"
                      {...props}
                    >
                      <title>{"left"}</title>
                      <path d="M257 64L291 98 128 262 291 426 257 460 61 262 257 64Z" />
                    </svg>
                  </Button>
                )}

                {NextShow && (
                  <Button onClick={() => fnNextPrev("next")} className="np-btn">
                    <svg
                      fill="#fff"
                      width="20px"
                      height="20px"
                      viewBox="-77 0 512 512"
                      xmlns="http://www.w3.org/2000/svg"
                      {...props}
                    >
                      <title>{"right"}</title>
                      <path d="M98 460L64 426 227 262 64 98 98 64 294 262 98 460Z" />
                    </svg>
                  </Button>
                )}
              </div>
            </div>
            {MailList.Loading ? (
              <Loader />
            ) : (
              <>
                {MailList.data?.length > 0 ? (
                  MailList.data?.map((item, index) => (
                    <SingleMail
                      SingleClick={getEmailDetail}
                      data={item}
                      type={props?.type}
                      key={index}
                      DeleteClick={Deletemail}
                      EmailDetail={MailDetail}
                    />
                  ))
                ) : (
                  <div className="no-data-found mt-2">
                    <div className="no-data-found-content">
                      <h6>Nothing in {props?.type}</h6>
                      <NoData />
                      <span>Looks empty over here.</span>
                    </div>
                  </div>
                )}
              </>
            )}
          </div>
        </Col>
        {MailList?.data?.length > 0 && (
          <Col sm={7} className="ps-0">
            <MailDetailBox MailDetail={MailDetail} />
          </Col>
        )}
      </Row>
    </>
  );
};

export default MailListBox;
