import axios from "axios";
import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import FloatingLabel from "react-bootstrap/FloatingLabel";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { Commonservice } from "../CommonServices/Commonservice";
import { AllcountSlice } from "../ReduxToolkit/EmailSlice";

const SendMail = (props) => {
  let dispatch = useDispatch();
  const [EmailLoader, setEmailLoader] = useState(false);
  let Token = useSelector((state) => state.UserData.M_Token);
  const [input, setInput] = useState({
    to: "",
    subject: "",
    comment: "",
    errors: {
      to: "",
      subject: "",
      comment: "",
      ValidationRules: [
        {
          FieldName: "to",
          ValidationType: "required",
          ValidationMessage: "This Field is a required",
        },
        {
          FieldName: "subject",
          ValidationType: "required",
          ValidationMessage: "This Field is a required",
        },

        {
          FieldName: "to",
          ValidationType: "email",
          ValidationMessage: "Invalid email address format",
        },
      ],
    },
  });

  async function InboxCount() {
    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.get(
        "https://graph.microsoft.com/v1.0/me/mailFolders/Inbox/messages?$count=true",
        {
          headers: headers,
        }
      );
      const inboxCount = response?.data["@odata.count"];
      return inboxCount;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
  async function JunkCount() {
    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.get(
        "https://graph.microsoft.com/v1.0/me/mailFolders/JunkEmail/messages?$count=true",
        {
          headers: headers,
        }
      );
      const inboxCount = response?.data["@odata.count"];
      return inboxCount;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
  async function DraftsCount() {
    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.get(
        "https://graph.microsoft.com/v1.0/me/mailFolders/Drafts/messages?$count=true",
        {
          headers: headers,
        }
      );
      const inboxCount = response?.data["@odata.count"];
      return inboxCount;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
  async function SentCount() {
    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.get(
        "https://graph.microsoft.com/v1.0/me/mailFolders/SentItems/messages?$count=true",
        {
          headers: headers,
        }
      );
      const inboxCount = response?.data["@odata.count"];
      return inboxCount;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
  async function DeleteCount() {
    const bearer = `Bearer ${Token}`;
    const headers = {
      Authorization: bearer,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.get(
        "https://graph.microsoft.com/v1.0/me/mailFolders/DeletedItems/messages?$count=true",
        {
          headers: headers,
        }
      );
      const inboxCount = response?.data["@odata.count"];
      return inboxCount;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }

  async function fetchCounts() {
    try {
      const [inboxCount, junkCount, draftsCount, sentCount, deletecount] =
        await Promise.all([
          InboxCount(),
          JunkCount(),
          DraftsCount(),
          SentCount(),
          DeleteCount(),
        ]);
      let AllCount = {
        inbox: inboxCount,
        junk: junkCount,
        drafts: draftsCount,
        sent: sentCount,
        delete: deletecount,
      };
      dispatch(AllcountSlice(AllCount));
    } catch (error) {
      console.error(error);
    }
  }

  const fnSendEmail = async (e) => {
    e.preventDefault();
    let obj = Commonservice.fnCheckValidationOfObject(input);
    setInput({
      ...obj.obj,
    });
    if (obj.isValid) {
      setEmailLoader(true);
      const bearer = `Bearer ${Token}`;
      const config = {
        headers: {
          Authorization: bearer,
          "Content-Type": "application/json",
        },
      };
      const data = {
        message: {
          subject: input.subject,
          body: {
            contentType: "HTML",
            content: input.comment,
          },
          toRecipients: [
            {
              emailAddress: {
                address: input.to,
              },
            },
          ],
        },
      };
      try {
        const response = await axios.post(
          `https://graph.microsoft.com/v1.0/me/sendMail`,
          data,
          config
        );        
        if (response?.status === 202) {
          fetchCounts();
          setEmailLoader(false);
          toast.success("You have sent successfully!");
          props.onHide();
        }
      } catch (error) {
        console.error(error);
        throw error; // Rethrow the error to handle it in the caller function
      }
    }
  };

  return (
    <Modal className="emailModal" size="lg" {...props}>
      <Modal.Header closeButton>
        <Modal.Title>New Message</Modal.Title>
      </Modal.Header>
      <Form onSubmit={(e) => fnSendEmail(e)}>
        <Modal.Body>
          <Form.Control
            type="email"
            placeholder="To"
            value={input.to}
            onChange={(e) =>
              setInput((prevInput) => ({
                ...prevInput,
                to: e.target.value,
                errors: {
                  ...prevInput.errors,
                  to: "", // Clear previous error when user starts typing
                },
              }))
            }
            isInvalid={input.errors.to}
          />
          {input.errors.to && (
            <Form.Control.Feedback type="invalid">
              {input.errors.to}
            </Form.Control.Feedback>
          )}
          {/* Other input fields for subject and comment */}
          <Form.Control
            type="text"
            placeholder="Add a Subject"
            value={input.subject}
            onChange={(e) =>
              setInput((prevInput) => ({
                ...prevInput,
                subject: e.target.value,
                errors: {
                  ...prevInput.errors,
                  subject: "", // Clear previous error when user starts typing
                },
              }))
            }
            isInvalid={input.errors.subject}
          />
          {input.errors.subject && (
            <Form.Control.Feedback type="invalid">
              {input.errors.subject}
            </Form.Control.Feedback>
          )}

          {/* Comment input field */}
          <Form.Control
            as="textarea"
            placeholder="Leave a comment here"
            style={{ height: "300px" }}
            value={input.comment}
            onChange={(e) =>
              setInput((prevInput) => ({
                ...prevInput,
                comment: e.target.value,
                errors: {
                  ...prevInput.errors,
                  comment: "", // Clear previous error when user starts typing
                },
              }))
            }
            isInvalid={input.errors.comment}
          />
          {input.errors.comment && (
            <Form.Control.Feedback type="invalid">
              {input.errors.comment}
            </Form.Control.Feedback>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button type="submit" variant="primary">
            {EmailLoader ? "Loading..." : "Send"}
          </Button>
        </Modal.Footer>
      </Form>
    </Modal>
  );
};

export default SendMail;
