import { useSelector } from "react-redux";
import { Navigate, Route, BrowserRouter as Router, Routes } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import Layout from "./AllRoutes/CommonComponents/Layout";
import "./App.css";
import LoginPage from "./AuthRoutes/LoginPage";

function App() {
  let Token = useSelector((state) => state.UserData.M_Token);
  const Is_Login = Token ? true : false;

  const AllRoutes = () => {
    return (
      <>
        <Route path="/*" element={<Layout />} />
      </>
    );
  };

  const AuthRoutes = () => {
    return (
      <>   

        <Route path="*" element={<Navigate to="/" />} />
        <Route path="/" element={<LoginPage />} />
      </>
    );
  };

  return (
    <>
      <ToastContainer />
      <Router  basename={process.env.PUBLIC_URL}>
        <Routes>{Is_Login ? AllRoutes() : AuthRoutes()}</Routes>
      </Router>
    </>
  );
}

export default App;
