import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  AllCount: localStorage.getItem("Allcount")
    ? JSON.parse(localStorage.getItem("Allcount"))
    : null,
};

const EmailSlice = createSlice({
  name: "email",
  initialState,
  reducers: {
    AllcountSlice: (state, action) => {
      state.AllCount = action.payload;
      localStorage.setItem("Allcount", JSON.stringify(action.payload));
    },
  },
});

export const { AllcountSlice } = EmailSlice.actions;
export default EmailSlice.reducer;
