import axios from "axios";
import React, { useEffect, useState } from "react";
import { Col, Row } from "react-bootstrap";
import { useSelector } from "react-redux";
import NoData from "../../Assets/NoData";
import { Commonservice } from "../../CommonServices/Commonservice";
import AudioIcon from "../../Assets/AudioIcon";
import Pdficon from "../../Assets/Pdficon";
import Xml from "../../Assets/Xml";
import Jsonfile from "../../Assets/Jsonfile";
import FileIcon from "../../Assets/FileIcon";
import WordIcon from "../../Assets/WordIcon";
import PPTicon from "../../Assets/PPTicon";
import AttachmentIcon from "../../Assets/AttachmentIcon";

const MailDetailBox = (props) => {
  let Token = useSelector((state) => state.UserData.M_Token);
  const [AttachmentData, setAttachmentData] = useState([]);

  const contentTypeToIcon = {
    "application/pdf": <Pdficon />,
    "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet": (
      <FileIcon />
    ),
    "application/vnd.google-apps.spreadsheet": <FileIcon />,
    "application/msword": <WordIcon />,
    "application/vnd.openxmlformats-officedocument.wordprocessingml.document": (
      <WordIcon />
    ),
    "application/vnd.ms-powerpoint": <PPTicon />,
    "application/vnd.openxmlformats-officedocument.presentationml.presentation":
      <PPTicon />,
    "application/zip": <FileIcon />,
    "application/x-rar-compressed": <FileIcon />,
    "application/x-tar": <FileIcon />,
    "application/x-7z-compressed": <FileIcon />,
    "application/x-gzip": <FileIcon />,
    "application/x-bzip2": <FileIcon />,
    "text/plain": <FileIcon />,
    "text/html": <FileIcon />,
    "text/css": <FileIcon />,
    "text/javascript": <Jsonfile />,
    "application/json": <Jsonfile />,
    "application/xml": <Xml />,
  };

  async function EmailAttachments(emailId) {
    const headers = {
      Authorization: `Bearer ${Token}`,
      "Content-Type": "application/json",
    };
    try {
      const response = await axios.get(
        `https://graph.microsoft.com/v1.0/me/messages/${emailId}/attachments`,
        { headers }
      );
      setAttachmentData(response?.data?.value);
    } catch (error) {
      console.error(error);
      throw error; // Re-throw the error for handling in the calling code
    }
  }

  function fnImg(value) {
    var imgMain = document.getElementsByTagName("img");
    if (imgMain.length > 0) {
      Array.from(imgMain).forEach((element) => {
        if (element.alt === value.name) {
          element.src = `data:${value.contentType};base64,${value.contentBytes}`;
        }
      });
    }
  }

  useEffect(() => {
    if (props?.MailDetail?.data?.id) {
      EmailAttachments(props?.MailDetail?.data?.id);
    }
  }, [props]);

  return (
    <>
      {props?.MailDetail?.data !== undefined ? (
        <>
          <div className="maillist-header mt-2 p-2 d-flex justify-content-between align-items-center">
            <div>{props?.MailDetail?.data?.subject}</div>
          </div>
          <div className="email-detail mt-2">
            <div className="email-detail-content">
              <div class="SingleMail d-flex justify-content-between align-items-center">
                <div class="SingleMail-img">
                  {props?.MailDetail?.data?.sender?.emailAddress?.name[0]}
                </div>
                <div class="SingleMail-content cust-width">
                  <div class="SingleMail-content-top">
                    <div class="SingleMail-content-top-name">
                      {props?.MailDetail?.data?.sender?.emailAddress?.name}
                      {"<" +
                        props?.MailDetail?.data?.sender?.emailAddress?.address +
                        ">"}
                    </div>
                    {props?.MailDetail?.data?.hasAttachments && (
                      <div className="SingleMail-content-top-icon">
                        <AttachmentIcon />
                      </div>
                    )}
                  </div>
                  <div class="SingleMail-content-center">
                    {props?.MailDetail?.data?.toRecipients && (
                      <div class="SingleMail-content-center-first">
                        To :{" "}
                        {
                          props?.MailDetail?.data?.toRecipients[0]?.emailAddress
                            ?.name
                        }
                      </div>
                    )}
                    <div>
                      {Commonservice.getDateTimeFormatMailDetail(
                        props?.MailDetail?.data?.receivedDateTime
                      )}
                    </div>
                  </div>
                </div>
                <span onClick={() => alert()}></span>
              </div>
              <div
                className="html-content main"
                dangerouslySetInnerHTML={{
                  __html: props?.MailDetail?.data?.body?.content,
                }}
              ></div>
            </div>
            {AttachmentData?.length > 0 && (
              <div className="html-content">
                <Row>
                  {AttachmentData?.length > 0 &&
                    AttachmentData.map(
                      (atc_item, attachmentIndex) =>
                        atc_item !== undefined && (
                          <Col md={4} className="attatch-card">
                            <div
                              key={attachmentIndex}
                              className="attachment mb-3 tab-content p-2"
                            >
                              {atc_item.contentType.startsWith("image/") ? (
                                <>
                                  {fnImg(atc_item)}
                                  <div className="image-attachment">
                                    <img
                                      src={`data:${atc_item.contentType};base64,${atc_item.contentBytes}`}
                                      alt={atc_item.name}
                                      className="image-attachment-image"
                                    />
                                    <p className="attachment-name my-3">
                                      {atc_item.name}
                                    </p>
                                    <a
                                      href={`data:${atc_item.contentType};base64,${atc_item.contentBytes}`}
                                      download={atc_item.name}
                                      className="btn btn-primary w-100 blue-bg"
                                    >
                                      Download
                                    </a>
                                  </div>
                                </>
                              ) : (
                                <div className="non-image-attachment">
                                  <div className="image-attachment-image d-flex justify-content-center    align-items-center">
                                    {contentTypeToIcon[
                                      atc_item.contentType
                                    ] || <FileIcon />}
                                  </div>
                                  <p className="attachment-name my-3">
                                    {atc_item.name}
                                  </p>
                                  <a
                                    href={`data:${atc_item.contentType};base64,${atc_item.contentBytes}`}
                                    download={atc_item.name}
                                    className="btn btn-primary w-100 blue-bg"
                                  >
                                    Download
                                  </a>
                                </div>
                              )}
                            </div>
                          </Col>
                        )
                    )}
                </Row>
              </div>
            )}
          </div>
        </>
      ) : (
        <div className="no-data-found">
          <div className="no-data-found-content">
            <h5>Nothing in Junk</h5>
            <NoData />
            <span>Looks empty over here.</span>
          </div>
        </div>
      )}
    </>
  );
};

export default MailDetailBox;
