import React from "react";
import LoginHeader from "./CommonComponents/LoginHeader";
import { SignInButton } from "./CommonComponents/SignInButton";
import LoginFooter from "./CommonComponents/LoginFooter";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import rightImg from "../Assets/Prakash_DPPNG.png";
// import rightImg from "../Assets/bgImg1.png";

const LoginPage = () => {
  return (
    <>
      <LoginHeader />
      <div className="bg-img">
        <Container>
          <Row className="login-row">
            <Col sm={4}>
              <div className="login-body">
                <h1 className="mb-3">Welcome to microsoft Outlook</h1>
                <p className="mb-4">
                  Sign in now to connect, organize, and get things done with
                  your email.
                </p>
                <SignInButton text="Sign in with your Outlook email" />
              </div>
            </Col>
            <Col sm={8} className="d-flex  justify-content-center">
              <div className="right-img1">
                <img src={rightImg} alt={rightImg} />
              </div>
            </Col>
          </Row>
        </Container>
      </div>
      <LoginFooter />
    </>
  );
};

export default LoginPage;
