import React, { useState } from "react";
import { Link, Navigate, Route, Routes } from "react-router-dom";
import LinkedIcin from "../../Assets/LinkedIcin";
import LoginHeader from "../../AuthRoutes/CommonComponents/LoginHeader";
import Archive from "../Archive";
import Drafts from "../Drafts";
import Inbox from "../Inbox";
import Junk from "../Junk";
import Sent from "../Sent";
import Trash from "../Trash";
import Sidebar from "./Sidebar";

const Layout = () => {
  const [URLfromHeaderTOSidebar, setURLfromHeaderTOSidebar] = useState();
  function OnLogoClick(data) {
    setURLfromHeaderTOSidebar(data);
  }
  return (
    <>
      <LoginHeader OnLogoClick={OnLogoClick} />
      <div className="d-flex bg-white1">
        <Sidebar
          OnLogoClick={OnLogoClick}
          URLfromHeaderTOSidebar={URLfromHeaderTOSidebar}
        />
        <div className="all-route-main">
          <Routes>
            <Route path="*" element={<Navigate to="/inbox" />} />
            <Route path="/inbox" element={<Inbox />} />
            <Route path="/sent" element={<Sent />} />
            <Route path="/drafts" element={<Drafts />} />
            <Route path="/trash" element={<Trash />} />
            <Route path="/junk" element={<Junk />} />
            <Route path="/archive" element={<Archive />} />
          </Routes>
          <div className="inside-footer">
            <div>2024 - Made by Mr. Prakash</div>
            <div className="fott-icon">
              <Link target="_blank" to="https://www.linkedin.com/in/pek-s-kumar-ba2636125/">
                <LinkedIcin color="#fff" />
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Layout;
