import React, { useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import {
  Menu,
  MenuItem,
  ProSidebar,
  SidebarContent,
  SidebarHeader,
} from "react-pro-sidebar";
import "react-pro-sidebar/dist/css/styles.css";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import ArchiveIcon from "../../Assets/ArchiveIcon";
import DraftsIcon from "../../Assets/DraftsIcon";
import EmailIcon from "../../Assets/EmailIcon";
import InboxIcon from "../../Assets/InboxIcon";
import JunkIcon from "../../Assets/JunkIcon";
import SentIcon from "../../Assets/SentIcon";
import ThreeLineIcon from "../../Assets/ThreeLineIcon";
import TrashIcon from "../../Assets/TrashIcon";
import SendMail from "../../Modal/SendMail";

const Sidebar = (props) => {
  const [SendMailModalshow, setSendMailModalshow] = useState(false);
  let AllCount = useSelector((state) => state.EmailData.AllCount);

  const [menuCollapse, setMenuCollapse] = useState(false);
  const menuIconClick = () => {
    menuCollapse ? setMenuCollapse(false) : setMenuCollapse(true);
  };
  const [activeTab, setActiveTab] = useState();

  useEffect(() => {
    if (props?.URLfromHeaderTOSidebar !== undefined) {
      setActiveTab(props?.URLfromHeaderTOSidebar);
    }
  }, [props?.URLfromHeaderTOSidebar]);

  useEffect(() => {
    if (window.location.href) {
      let temp = window.location.href.split("/");
      setActiveTab(temp[temp?.length - 1]);
    }
  }, [activeTab]);

  return (
    <>
      <div id="header">
        <ProSidebar collapsed={menuCollapse}>
          <SidebarHeader className="  p-2   ">
            <div className="closemenu" onClick={menuIconClick}>
              <ThreeLineIcon />
            </div>
          </SidebarHeader>
          <SidebarContent>
            <Menu iconShape="square">
              <Button
                onClick={() => setSendMailModalshow(true)}
                className="d-flex justify-content-between
 align-items-center blue-bg new-mail-btn mb-2"
              >
                <EmailIcon />
                {!menuCollapse && <span className="ps-2"> New mail</span>}
              </Button>
              <Link
                to={"/inbox"}
                className={
                  activeTab === "inbox" ? "active MenuItem" : "MenuItem"
                }
                onClick={() => setActiveTab("inbox")}
              >
                <MenuItem
                  active={activeTab === "inbox" ? true : false}
                  icon={<InboxIcon />}
                >
                  {!menuCollapse && (
                    <div className="d-flex justify-content-between">
                      <div>Inbox</div>
                      <div>{AllCount?.inbox > 0 ? AllCount?.inbox : ""}</div>
                    </div>
                  )}
                </MenuItem>
              </Link>
              <Link
                to={"/junk"}
                onClick={() => {
                  setActiveTab("junk");
                  props?.OnLogoClick("junk");
                }}
              >
                <MenuItem active={activeTab === "junk"} icon={<JunkIcon />}>
                  {!menuCollapse && (
                    <div className="d-flex justify-content-between">
                      <div>Junk Email</div>
                      <div>{AllCount?.junk > 0 ? AllCount?.junk : ""}</div>
                    </div>
                  )}
                </MenuItem>
              </Link>
              <Link
                to={"/drafts"}
                onClick={() => {
                  props?.OnLogoClick("drafts");
                  setActiveTab("drafts");
                }}
              >
                <MenuItem
                  active={activeTab === "drafts" ? true : false}
                  icon={<DraftsIcon />}
                >
                  {!menuCollapse && (
                    <div className="d-flex justify-content-between">
                      <div>Drafts</div>
                      <div>{AllCount?.drafts > 0 ? AllCount?.drafts : ""}</div>
                    </div>
                  )}
                </MenuItem>
              </Link>
              <Link
                to={"/sent"}
                onClick={() => {
                  // props?.OnLogoClick("sent");
                  setActiveTab("sent");
                }}
              >
                <MenuItem
                  active={activeTab === "sent" ? true : false}
                  icon={<SentIcon />}
                >
                  {!menuCollapse && (
                    <div className="d-flex justify-content-between">
                      <div>Sent Items</div>
                      <div>{AllCount?.sent > 0 ? AllCount?.sent : ""}</div>
                    </div>
                  )}
                </MenuItem>
              </Link>
              <Link
                to={"/trash"}
                onClick={() => {
                  props?.OnLogoClick("trash");
                  setActiveTab("trash");
                }}
              >
                <MenuItem
                  active={activeTab === "trash" ? true : false}
                  icon={<TrashIcon />}
                >
                  {!menuCollapse && (
                    <div className="d-flex justify-content-between">
                      <div>Deleted Items</div>
                      <div>{AllCount?.delete > 0 ? AllCount?.delete : ""}</div>
                    </div>
                  )}
                </MenuItem>
              </Link>
              <Link
                to={"/archive"}
                onClick={() => {
                  props?.OnLogoClick("archive");
                  setActiveTab("archive");
                }}
              >
                <MenuItem
                  active={activeTab === "archive" ? true : false}
                  icon={<ArchiveIcon />}
                >
                  {!menuCollapse && (
                    <div className="d-flex justify-content-between">
                      <div>Archive</div>
                      {/* <div>{AllCount?.delete > 0 ? AllCount?.delete : ""}</div> */}
                    </div>
                  )}
                </MenuItem>
              </Link>
            </Menu>
          </SidebarContent>
          {/* <SidebarFooter>
            <Menu iconShape="square">
              <MenuItem 
             icon={<YoutubeIcon />}
              >Logout</MenuItem>
            </Menu>
          </SidebarFooter> */}
        </ProSidebar>
      </div>

      {SendMailModalshow && (
        <SendMail
          show={SendMailModalshow}
          onHide={() => setSendMailModalshow(false)}
        />
      )}
    </>
  );
};

export default Sidebar;
