import React from "react";
import { useMsal } from "@azure/msal-react";
import DropdownButton from "react-bootstrap/DropdownButton";
import Dropdown from "react-bootstrap/esm/Dropdown";
import { useDispatch, useSelector } from "react-redux";
import { M_Token, M_UserInfo } from "../../ReduxToolkit/UserSlice";
import { Button } from "react-bootstrap";
import Logoplaceholder from "../../Assets/Logoplaceholder";
import LogoutIcon from "../../Assets/LogoutIcon";

/**
 * Renders a sign-out button
 */
export const SignOutButton = () => {
  const UserInfo = useSelector((state) => state.UserData.M_UserInfo);

  let dispatch = useDispatch();
  const { instance } = useMsal();

  const handleLogout = (logoutType) => {
    if (logoutType === "popup") {
      dispatch(M_Token());
      dispatch(M_UserInfo());
      localStorage.clear();
      instance.logoutPopup({
        postLogoutRedirectUri: "/",
        mainWindowRedirectUri: "/",
      });
    } else if (logoutType === "redirect") {
      dispatch(M_Token());
      dispatch(M_UserInfo());
      localStorage.clear();
      instance.logoutPopup({
        postLogoutRedirectUri: "/",
        mainWindowRedirectUri: "/",
      });
    }
  };
  return (
    // <DropdownButton
    //   variant="primary"
    //   className="ml-auto"
    //   drop="left"
    //   title="Sign Out"
    // >
    //   <Dropdown.Item as="button" onClick={() => handleLogout("popup")}>
    //     Sign out using Popup
    //   </Dropdown.Item>
    //   <Dropdown.Item as="button" onClick={() => handleLogout("redirect")}>
    //     Sign out using Redirect
    //   </Dropdown.Item>
    // </DropdownButton>
    // <Button onClick={() => handleLogout("popup")}>Sign out</Button>

    <div
      className="d-flex  align-items-center header-login-btn"
      // className=" text-end"
      onClick={() => handleLogout("popup")}
    >
      <LogoutIcon />
      <span className="ps-2">Sign Out</span>
      {/* <div className="placeholder-img">
        <Logoplaceholder color="#fff" />
      </div> */}
    </div>
  );
};
