import React from "react";
import DeleteIcon from "../../Assets/DeleteIcon";
import { Commonservice } from "../../CommonServices/Commonservice";
import AttachmentIcon from "../../Assets/AttachmentIcon";
import { confirmAlert } from "react-confirm-alert";

const SingleMail = (props) => {  
  return (
    <>
      <div
        className={
          props?.data?.id === props?.EmailDetail?.data?.id
            ? "SingleMail d-flex justify-content-between align-items-center active"
            : "SingleMail d-flex justify-content-between align-items-center"
        }
      >
        <div
          onClick={() => props.SingleClick(props.data)}
          className="SingleMail-img"
        >
          {props.data?.sender?.emailAddress?.name[0]}
        </div>
        <div
          onClick={() => props.SingleClick(props.data)}
          className="SingleMail-content"
        >
          <div className="SingleMail-content-top">
            <div className="SingleMail-content-top-name">
              {props.data?.sender?.emailAddress?.name}
            </div>
            {props.data?.hasAttachments && (
              <div className="SingleMail-content-top-icon">
                <AttachmentIcon />
              </div>
            )}
          </div>
          <div className="SingleMail-content-center">
            <div className="SingleMail-content-center-first">
              {props.data?.subject}
            </div>
            <div className="SingleMail-content-center-date">
              {Commonservice.getDateTimeFormatMailList(
                props.data?.createdDateTime
              )}
            </div>
          </div>
          <div className="SingleMail-content-bottom">
            {props.data?.bodyPreview}
          </div>
        </div>
        <span
          onClick={() => {
            confirmAlert({
              title: "Confirm to delete?",
              message: "Are you sure to do this.",
              buttons: [
                {
                  label: "Yes",
                  onClick: () => props?.DeleteClick(props?.data?.id),
                },
                {
                  label: "No",
                },
              ],
            });
          }}
        >
          <DeleteIcon />
        </span>
      </div>
    </>
  );
};

export default SingleMail;
