import { useIsAuthenticated, useMsal } from "@azure/msal-react";
import React, { useEffect } from "react";
import Container from "react-bootstrap/Container";
import Dropdown from "react-bootstrap/Dropdown";
import Navbar from "react-bootstrap/Navbar";
import { useDispatch, useSelector } from "react-redux";
import PlaceholderImgUser from "../../Assets/Userplaceholder.png";
import Logo from "../../Assets/logo.png";
import WhiteLogo from "../../Assets/white-logo.png";
import { loginRequest } from "../../CommonServices/authConfig";
import axios from "axios";
import { Link } from "react-router-dom";
import { M_Token, M_UserInfo } from "../../ReduxToolkit/UserSlice";
import { SignInButton } from "./SignInButton";
import { SignOutButton } from "./SignOutButton";

const LoginHeader = (props) => {
  let UserInfo = useSelector((state) => state.UserData.M_UserInfo);
  let Token = useSelector((state) => state.UserData.M_Token);
  let dispatch = useDispatch();
  let navigate = useDispatch();
  const { instance, accounts } = useMsal();
  const isAuthenticated = useIsAuthenticated();

  function getTokenWithUserInfo() {
    instance
      .acquireTokenSilent({
        ...loginRequest,
        account: accounts[0],
      })
      .then((response) => {
        dispatch(M_Token(response?.accessToken));
        // getProfileDetail(response.accessToken);
      })
      .catch((error) => {
        console.error("Error acquiring access token:", error);
      });
  }
  useEffect(() => {
    getTokenWithUserInfo();
  }, [isAuthenticated]);

  async function getProfileDetail(accessToken) {
    const bearer = `Bearer ${accessToken}`;
    const config = {
      headers: {
        Authorization: bearer,
      },
    };
    try {
      const response = await axios.get(
        `https://graph.microsoft.com/v1.0/me`,
        config
      );
      if (
        response?.data !== undefined &&
        response?.data !== "" &&
        response?.data !== null
      ) {
        dispatch(M_UserInfo(response?.data));
        getProfilePic(accessToken, response?.data);
      }
    } catch (error) {
      console.log(error);
      throw error;
    }
  }

  async function getProfilePic(accessToken, Udata) {
    const bearer = `Bearer ${accessToken}`;
    const config = {
      headers: {
        Authorization: bearer,
      },
    };
    try {
      const response = await axios.get(
        `https://graph.microsoft.com/v1.0/me/photo/$value`,
        {
          ...config,
          responseType: "blob", // Set responseType to 'blob'
        }
      );
      const blob = new Blob([response.data]);
      const reader = new FileReader();
      reader.readAsDataURL(blob);
      reader.onloadend = () => {
        let base64ImageString = reader.result.split(",")[1];
        let srcValue = "data:image/png;base64," + base64ImageString;

        let temp = {
          ...Udata,
          ProfilePic: srcValue,
        };
        dispatch(M_UserInfo(temp));
      };
    } catch (error) {
      console.log("Error:", error);
      throw error;
    }
  }

  useEffect(() => {
    if (Token) {
      getProfileDetail(Token);
    }
  }, [Token]);

  return (
    <>
      <Navbar className={isAuthenticated ? "blue-bg white-text" : "white-bg"}>
        <Container fluid>
          <Link
            className="d-flex py-2"
            to="/inbox"
            // onClick={() => navigate("/inbox")}
            onClick={() => {
              // navigate("/inbox");
              props?.OnLogoClick("inbox");
            }}
          >
            <img
              src={isAuthenticated ? WhiteLogo : Logo}
              className="d-inline-block align-top logo-img"
              alt={Logo}
            />
          </Link>

          {isAuthenticated ? (
            <Dropdown>
              <Dropdown.Toggle variant="success" id="dropdown-basic">
                {UserInfo?.displayName !== " "
                  ? UserInfo?.displayName
                  : UserInfo?.mail}
                <img
                  src={UserInfo?.ProfilePic}
                  onError={({ currentTarget }) => {
                    currentTarget.onerror = null;
                    currentTarget.src = PlaceholderImgUser;
                  }}
                  className="d-inline-block align-top prof-img"
                  alt={UserInfo?.ProfilePic}
                />
              </Dropdown.Toggle>
              <Dropdown.Menu>
                <Dropdown.Item>
                  <SignOutButton />
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          ) : (
            <SignInButton text="Sign in" />
          )}
        </Container>
      </Navbar>
    </>
  );
};

export default LoginHeader;
